.top {
  padding: 40px 140px 80px 110px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  height: calc(680px - 80px);
  position: relative;

  &__background {
    z-index: -1;
    position: absolute;
    width: 100%;
    height: 680px;
    top: -80px;
    left: 0;
    text-align: left;

    &--image {
      object-position: top;
      width: 100%;
      height: 100%;
    }
  }

  &__heading {
    font-size: 70px;
    line-height: 1;

    &--color {
      color: #309930;
    }
  }

  &__text {
    width: 383px;
    font-size: 16px;
    line-height: 24px;
    margin-top: 23px;
  }
}
